export const pageQuery = graphql`
  query {
    imageOne: file(relativePath: { eq: "gemeindehaus_front.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`